/** @jsxImportSource @emotion/react */
import React from 'react'
import Drawer from '@mui/material/Drawer'
import Button from '@mui/material/Button'

import { UserName } from './UserName'
import { useApi } from '../../data/api'

export interface SideNavBarProps {
  className?: string
  isOpen: boolean
  onClose: () => void
}

export const SideNavBar = ({ className, isOpen, onClose }: SideNavBarProps): JSX.Element => {
  const api = useApi()
  return (
    <Drawer anchor="left" open={isOpen} onClose={() => onClose()} className={className}>
      <div css={{ minWidth: '20em', height: '100%', display: 'flex', flexDirection: 'column' }}>
        <UserName />
        <div css={{ display: 'flex', justifyContent: 'center' }}>
          <Button
            onClick={() => {
              api.signOut()
            }}
          >
            Sign Out
          </Button>
        </div>
        <div
          css={{
            marginTop: 'auto',
            padding: '1em 0',
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
            gap: '0.25em',
          }}
        >
          <span>v{process.env.REACT_APP_VERSION}</span>
          <a href="https://github.com/namc-utah/namc-bug-db/issues/new" target="_blank" rel="noreferrer">
            Report Issue
          </a>
        </div>
      </div>
    </Drawer>
  )
}
