import gql from 'graphql-tag'
import {
  TaxaLevelLookup as GraphTaxaLevelLookup,
  LifeStageLookup as GraphLifeStageLookup,
  NoteTypes as GraphNoteTypes,
} from '@namcbugdb/common'

import { Api } from '../api'
import { LifeStage, NoteType, TaxaLevel } from '../../types'

export const fetchLookupsQuery = gql`
  query {
    lookups {
      taxa {
        lifeStages
        taxaLevels
      }
      sample {
        noteTypes
      }
    }
  }
`

export const fetchLookups =
  (api: Api) => async (): Promise<{ taxaLevels: TaxaLevel[]; lifeStages: LifeStage[]; noteTypes: NoteType[] }> => {
    const data = await api.query<{
      lookups: {
        taxa: {
          taxaLevels: GraphTaxaLevelLookup[]
          lifeStages: GraphLifeStageLookup[]
        }
        sample: {
          noteTypes: GraphNoteTypes[]
        }
      }
    }>(fetchLookupsQuery)
    const {
      lookups: {
        taxa: { taxaLevels, lifeStages },
        sample: { noteTypes },
      },
    } = data

    return {
      taxaLevels,
      lifeStages,
      noteTypes,
    }
  }
